.questionBox {
  height: 80px;
}
.desktopStepper {
  display: block;
  min-width: 600px;
  /* border: 1px solid red; */
}
.brainGames {
  font-size: large;
}
.vibrantss {
  color: #ffc107;
  font-size: 25px;
}
.mobileStepper {
  display: none;
}
.mobileScore {
  display: none;
}
.mobileQuizPageAnimation{
    display: none;
  }

@media only screen and (max-width: 768px) {
  .leftQuizPage {
    display: none;
  }
  .mobileStepper {
    display: block;
  }
  .desktopStepper {
    display: none;
  }
  .questionBox {
    height: 100px;
  }
  .mobileQuizPageAnimation{
    display: block;
  }
  .mobileQuizPageAnimationSize{
    height: 180px;
  }

  .brainGames {
    display: none;
  }
  .desktopScore {
    display: none;
  }

  .mobileScore {
    display: block;
  }
}
